        width: auto;
        text-align: center;
    }

    svg {
        width: 100%;
        max-width: 400px;
        margin-top: 1em;
        margin-bottom: 1em;
    }

    .modal {
        position: fixed;
        z-index: 1;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        background-color: rgba(0, 0, 0, 0.4);
    }

    .modal-content {
        background-color: var(--modal-bg-color);
        margin: 6em auto;
        padding: 10px;
        border: 1px solid var(--border-color);
        border-radius: 10px;
        max-width: 400px;
        position: relative;
    }
    .modal-content > ul {
        text-align: left;
    }

    .solutions-container {
        text-align: left;
        max-height: 60vh;
        overflow-y: auto;
        padding-right: 10px;
    }

    .solutions-container p {
        margin-bottom: 1.5em;
        line-height: 1.4;
    }

    .close-button {
        position: absolute;
        top: -5px;
        right: 0px;
        background: transparent;
        border: none;
