        font-weight: bold;
        color: var(--text-color);
        cursor: pointer;
    }

    .close-button:hover,
    .close-button:focus {
        color: #ff3e00;
        outline: none;
    }

    @keyframes blinker {
        50% {
            opacity: 0;
        }
    }

    @media (max-width: 350px) {
        h1 {
            font-size: 2.5em;
        }
        .modal-content {
            margin: 5.5em auto;
        }
    }

    .current {
        position: relative;
        font-size: x-large;
        line-height: 1;
        height: auto;
    }

    @keyframes win-bounce {
        0% {
            transform: translateY(0);
        }
        40% {
            transform: translateY(-8px);
        }
        70% {
            transform: translateY(2px);
        }
        100% {
            transform: translateY(0);
        }
    }

    @keyframes shake-red {
        0% {
            transform: translateX(0);
            color: var(--text-color);
        }
        25% {
            transform: translateX(-4px);
            color: red;
        }
        50% {
            transform: translateX(4px);
            color: red;
        }
        75% {
            transform: translateX(-4px);
            color: red;
        }
        100% {
            transform: translateX(0);
            color: var(--text-color);
        }
    }

    .shake {
        display: inline;
        animation: shake-red 0.2s ease;
    }

    .current-container {
        position: relative;
        display: inline-block;
    }

    .current-container > .caret {
        position: absolute;
        left: 100%;
        margin-left: 0.1em;
        top: 50%;
        transform: translateY(-50%);
        font-size: inherit;
        line-height: 1;
    }

    .current-container > span {
        display: inline-block;
        line-height: 1;
    }

    .github-banner {
        position: fixed;
        top: 1em;
        right: 1em;
        background: var(--button-bg-color);
        color: var(--text-color);
        border: 2px solid var(--button-text-color);
        border-radius: 30px;
        padding: 10px 20px;
        cursor: pointer;
        box-shadow: 0px 0px 12px var(--text-color);
        transition: all 0.3s ease;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        width: auto;
        max-width: 200px;
        height: 40px;
        box-sizing: border-box;
    }

    .github-banner:not(.expanded) {
        max-width: 40px;
        padding: 10px;
    }

    .banner-content {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    .github-banner:not(.expanded) .banner-content {
        gap: 0;
        justify-content: center;
    }

    .github-icon {
        width: 24px;
        height: 24px;
        fill: var(--text-color);
        flex-shrink: 0;
    }

    .title {
        color: var(--text-color);
        text-decoration: none;
    }

    .text-content {
        white-space: nowrap;
        overflow: hidden;
        font-weight: 600;
        font-size: 14px;
        transition:
            max-width 0.3s ease-in-out,
            opacity 0.2s ease-in-out;
        max-width: 150px;
        opacity: 1;
    }

    .github-banner:not(.expanded) .text-content {
        opacity: 0;
        max-width: 0;
        transition-delay: 0s;
    }

    /* Menu Button Styles */
    .menu-button {
        position: fixed;
        top: 1em;
        left: 1em;
        background: transparent;
        border: none;
        cursor: pointer;
        z-index: 1001;
        width: auto;
        height: 40px;
        display: flex;
        align-items: center;
        transition: all 0.3s ease;
    }

    .menu-icon {
        display: flex;
        flex-direction: column;
        gap: 6px;
        width: 25px;
        height: auto;
    }

    .line {
        width: 100%;
        height: 3px;
        background: var(--text-color);
        border-radius: 2px;
        transition: transform 0.3s ease-in-out;
    }

    /* Sidebar Styles */
    .sidebar-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 99;
        opacity: 0;
        transition: opacity 0.3s ease-in-out;
        pointer-events: none;
    }

    .overlay-background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.6);
    }

    .sidebar {
        position: absolute;
        top: 0;
        left: 0;
        width: 15em;
        height: 100%;
        background: var(--modal-bg-color);
        box-shadow: 2px 0 10px rgba(0, 0, 0, 0.3);
        z-index: 100;
        display: flex;
        flex-direction: column;
        transform: translateX(-100%);
        transition: transform 0.3s ease-in-out;
    }

    .sidebar-link {
        display: flex;
        align-items: center;
        padding-left: 1em;
        border: 0;
        margin: 0;
        display: flex;
        cursor: pointer;
        align-items: center;
        width: 100%;
        font-size: 1em;
        height: 2.5em;
        color: var(--text-color);
        transition: all 0.2s ease;
        position: relative;
        z-index: 101;
        background: var(--modal-bg-color);
        text-align: left;
        justify-content: flex-start;
    }

    .sidebar-link:hover {
        background: var(--button-bg-color);
        text-decoration: none;
    }
    .sidebar-link:hover .glow-text {
        text-shadow: 0 0 10px var(--text-color);
    }

    /* Optional: Add transition for smooth glow effect */
    .glow-text {
        transition: text-shadow 0.2s ease;
    }
    .sidebar-icon {
        width: 20px;
        height: 20px;
        flex-shrink: 0;
    }

    .sidebar-link:hover .sidebar-icon {
        text-shadow: 0 0 8px var(--text-color);
        filter: drop-shadow(0 0 4px var(--text-color));
    }

    .glow-text,
    .sidebar-icon {
        transition: all 0.2s ease;
    }

    /* When sidebar is visible */
    .sidebar-overlay {
        opacity: 1;
        pointer-events: all;
    }

    .sidebar {
        transform: translateX(0);
    }

    /* Adjust menu button position when sidebar is open */
    .menu-button.sidebar-open {
        left: 16em; /* Match sidebar width */
        transition: left 0.3s ease-in-out;
    }

    @font-face {
        font-family: "Squaresville";
        src: url("./SQUARESV.TTF") format("truetype");
        font-weight: 900;
        font-style: normal;
    }

    /* Responsive adjustments */
    @media (max-width: 768px) {
        .sidebar {
            width: 16em;
        }

        .menu-button {
            top: 0.5em;
            left: 0.5em;
        }

        .menu-button.sidebar-open {
            left: 15em;
            right: auto;
        }
    }

    @media (max-width: 480px) {
        .sidebar {
            width: 100%;
        }

        .menu-button.sidebar-open {
            left: auto;
            right: 0.5em;
        }
    }

    .confetti-container {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        pointer-events: none;
        z-index: 9998;
        overflow: hidden;
    }

    .confetti-piece {
        position: absolute;
        bottom: 0;
        left: 50%; /* center bottom */
        width: 8px;
        height: 8px;
        opacity: 0;
        pointer-events: none;
        animation-name: confetti-explode;
        animation-timing-function: ease-out;
        animation-fill-mode: forwards;
        will-change: transform;
    }

    @keyframes confetti-explode {
        0% {
            transform: translate(0, 0) rotate(0deg);
            opacity: 1;
        }
        10% {
            /* quick upward blast to top */
            transform: translate(var(--spread, 0px), -100vh) rotate(180deg);
            opacity: 1;
        }

        /* now wobble every 5% on the way down */
        15% {
            transform: translate(
                    calc(var(--spread, 0px) * 0.9 + var(--wobble, 0px)),
                    -85vh
                )
                rotate(200deg);
        }
        20% {
            transform: translate(
                    calc(var(--spread, 0px) * 1.1 - var(--wobble, 0px)),
                    -70vh
                )
                rotate(220deg);
        }
        25% {
            transform: translate(
                    calc(var(--spread, 0px) * 0.95 + var(--wobble, 0px)),
                    -60vh
                )
                rotate(240deg);
        }
        30% {
            transform: translate(
                    calc(var(--spread, 0px) * 1.05 - var(--wobble, 0px)),
                    -50vh
                )
                rotate(260deg);
        }
        35% {
            transform: translate(
                    calc(var(--spread, 0px) * 0.9 + var(--wobble, 0px)),
                    -40vh
                )
                rotate(280deg);
        }
        40% {
            transform: translate(
                    calc(var(--spread, 0px) * 1.1 - var(--wobble, 0px)),
                    -35vh
                )
                rotate(300deg);
        }
        50% {
            transform: translate(
                    calc(var(--spread, 0px) * 0.95 + var(--wobble, 0px)),
                    -25vh
                )
                rotate(320deg);
        }
        60% {
            transform: translate(
                    calc(var(--spread, 0px) * 1.05 - var(--wobble, 0px)),
                    -15vh
                )
                rotate(340deg);
        }
        70% {
            transform: translate(
                    calc(var(--spread, 0px) * 0.9 + var(--wobble, 0px)),
                    -10vh
                )
                rotate(360deg);
        }
        85% {
            transform: translate(
                    calc(var(--spread, 0px) * 1.05 - var(--wobble, 0px)),
                    -5vh
                )
                rotate(370deg);
            opacity: 0;
        }
